.bg, .bg1, .bg2 {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
}
.bg {
    background-color:lightgray;
    z-index: -3;
}
.bg1 {
    -webkit-clip-path: polygon(72% 72%, 100% 0, 100% 100%, 0% 100%);
    clip-path: polygon(72% 72%, 100% 0, 100% 100%, 0% 100%);
    background-color: salmon;
    z-index: -1;
}
.bg2 {
    -webkit-clip-path: polygon(62% 64%, 100% 0, 100% 100%, 0% 100%);
    clip-path: polygon(62% 64%, 100% 0, 100% 100%, 0% 100%);
    background: lightpink;
    z-index: -2;
}
.bgcontact {
    background-color: orange;
    width: 100%;
    height: 45%;
    position: absolute;
    opacity: 0.5;
    z-index: -2;
    mix-blend-mode: multiply;
}
#contact{
	padding: 20px;
}
#contact h3{
	color: black;
}


